*{
    font-family: Helvetica, Arial, sans-serif;
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}

body{
    padding: 60px;
}

input{
    font-size: 1.2rem;
    width: 100%;
    color: grey;
    margin-top: 10px;
    margin-bottom: 10px;
}

.card-grid{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}


.card{
    align-content: center;
    border: 1px solid grey;
    border-radius: 6px;
    margin-bottom: 10px;
    margin-left: 40px;
    margin-top: 10px;
    overflow: hidden;
}

.card p{
    padding: 5px;
    text-align: center;
}

.card img{
    max-height: 170px;
}